import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController } from 'ionic-angular';
import { Http,RequestOptions, Headers } from '@angular/http';
import { HttpClient } from '@angular/common/http';
import { DataProvider } from '../../providers/data/data';
import { Storage } from '@ionic/storage';



/**
 * Generated class for the NuevojuegoPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-nuevojuego',
  templateUrl: 'nuevojuego.html',
})
export class NuevojuegoPage {


	nuevojuego:any={}
	club_id:any;

  constructor(public storage:Storage,public dataprovider :DataProvider,public view: ViewController,public navCtrl: NavController,private http: HttpClient, public navParams: NavParams) {
  	



       this.storage.get('usuario').then((val) => {

         if(val){

             this.club_id=val.club

         }
 			
    
        })




  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad NuevojuegoPage');
  }


//'/add/juego' :: addcodigo,addclub,addempresa,adddescripcion,addusuario,addproceso



  closeModal(){


  
    

    this.view.dismiss()
}


agregajuego(data){






data.addclub=this.club_id
data.addempresa=1
data.addusuario=1
data.addproceso=1



		 this.http.post('http://comunica7.com/apirestsmartgoal/public/add/juego',data)
    .subscribe(
      data => {

          console.log('ooo',data)



          this.closeModal()

   
         


        },

      error =>{



      })



}




}



