import { Component, ViewChild } from '@angular/core';
import { IonicPage, NavController, NavParams,App } from 'ionic-angular';
import { HttpClient } from '@angular/common/http';
import { Http , Response } from '@angular/http';
import { HomePage } from '../home/home';
import { Storage } from '@ionic/storage';
import { DataProvider } from '../../providers/data/data';


@IonicPage()
@Component({
  selector: 'page-ingresar',
  templateUrl: 'ingresar.html',
})
export class IngresarPage {


	rootPage: any;
	usuario:any={'usuario':'omar','password':'123','club':1}
	clubes:any
  alerta:any;
  user:any;
  constructor(public dataprovider:DataProvider,public storage: Storage,public  nav: NavController,private http: HttpClient, public navParams: NavParams) {


 


     
  }

  ionViewDidLoad() {



     this.clubes = this.dataprovider.traeclubs('null')


   }


  ingresar(usuario){

  	console.log(usuario)


    this.dataprovider.ingresar(usuario).subscribe(data=>{


        this.user = data


        if(this.user.length>0){

            this.storage.set('usuario',usuario)

            this.nav.push(HomePage);

        }
        else{

          this.alerta = 'Usuario , Password o Club Incorrecto'

        }


         

    })


     

     



  }


  filtra(){


  }

}



