import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ModalController,AlertController,Platform } from 'ionic-angular';
import { HttpClient } from '@angular/common/http';
import { Http , Response } from '@angular/http';
import { Storage } from '@ionic/storage';
import { NuevojuegoPage } from '../../pages/nuevojuego/nuevojuego';
import { HomePage } from '../../pages/home/home';
import { DetallejuegoPage } from '../../pages/detallejuego/detallejuego';
import { DataProvider } from '../../providers/data/data';


/**
/**
 * Generated class for the JuegosComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'juegos',
  templateUrl: 'juegos.html'
})
export class JuegosComponent {

  text: string;
  club_id:any=1;
  juegos:any;
  juegoeliminar:any={};

  constructor(public storage :Storage,public dataprovider: DataProvider,private http: HttpClient,public modalCtrl: ModalController,public navCtrl:NavController ) {
    console.log('Hello JuegosComponent Component');
    this.text = 'Hello World';


    this.storage.get('usuario').then((val) => {

         if(val){

             this.club_id=val.club


             this.juegos=dataprovider.listajuegos(this.club_id)

         }
       
    
        })

   

     



  }



  ionViewDidLoad() {
    console.log('ionViewDidLoad DetallejuegoPage');

  
  
        
  }



  agregajuego(){

  	   let profileModal = this.modalCtrl.create(NuevojuegoPage, {});
   profileModal.onDidDismiss(data => {
     
   		this.juegos=this.dataprovider.listajuegos(this.club_id)

   });
   profileModal.present();




  }

  detalle(data){

  	this.navCtrl.push(DetallejuegoPage, {
      juego: data,
    })

  }


  play(data){

   
    this.navCtrl.push(HomePage, {
      play: data.Codigo,
    })




  }

  eliminar(data){

    console.log(data)

    //this.dataprovider.eliminajuego(data.Codigo,this.club_id)



    this.juegoeliminar.dproceso= 3
    this.juegoeliminar.dusuario= 1
    this.juegoeliminar.ddescripcion = 1


    console.log('http://comunica7.com/apirestsmartgoal/public/delete/juego/'+data.Codigo+'/'+this.club_id+'/1')

    //'/delete/juego/{dcodigo}/{dclub}/{dempresa}' :: ddescripcion,dusuario,dproceso

    this.http.put('http://comunica7.com/apirestsmartgoal/public/delete/juego/'+data.Codigo+'/'+this.club_id+'/1',this.juegoeliminar).subscribe(data=>{


        this.juegos=this.dataprovider.listajuegos(this.club_id)

    })





    
  }



}



