  calculapuntaje(data){

    let puntaje = 0


   
    if (data >= 36 ){ puntaje = 2}
    if (data >= 36*2) { puntaje= 1}
    if (data >= 36*3) { puntaje= -1}
    if (data >= 36*4) { puntaje= -2}

    //

   // if (data>= 218*3 && data <218*4) { puntaje = -1 } 


    if (data== 0){ puntaje = 3}

    console.log('puntaje...',puntaje)


    return puntaje

  }

  calculapuntajeY(data){

    let puntaje = 0


    console.log('Puntaje Y',data)

   
    if (data >= 5 ){ puntaje = 2}
    if (data >= 33) { puntaje= 1}
    if (data >= 60) { puntaje= -1}
    if (data >= 88) { puntaje= -2}

    //

   // if (data>= 218*3 && data <218*4) { puntaje = -1 } 


    if (data== 0){ puntaje = 3}

    console.log('puntaje...',puntaje)


    return puntaje

  }


