import { Component } from '@angular/core';
import { DataProvider } from '../../providers/data/data';
import { IonicPage, NavController, NavParams, ViewController, ModalController } from 'ionic-angular';
/**
 * Generated class for the FiltrohistorialComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'filtrohistorial',
  templateUrl: 'filtrohistorial.html'
})
export class FiltrohistorialComponent {

  text: string;
  listatiros:any;
  jugador:any='';

  filtro:any={}

  constructor(public dataprovider:DataProvider,public navParams: NavParams) {
   

  	this.listatiros = dataprovider.listatiros()


  	this.jugador= navParams.get("jugador")


  	console.log('jugador..',this.jugador)

  }

  buscar(data){

  	console.log(data)
  }

}



