import { Component } from '@angular/core';
import { DataProvider } from '../../providers/data/data';
import { Storage } from '@ionic/storage';
import { ModalController } from 'ionic-angular';
import { FiltrohistorialComponent } from '../../components/filtrohistorial/filtrohistorial';
/**
 * Generated class for the HistorialComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'historial',
  templateUrl: 'historial.html'
})
export class HistorialComponent {

  text: string;

  jugadores:any;
  juegos:any;
  sexos:any;
  club_id:any;

  constructor(public modalCtrl: ModalController,public storage:Storage,public dataprovider:DataProvider) {



  	  this.storage.get('usuario').then((val) => {

         if(val){

             this.club_id=val.club
             console.log('cofiifif',this.club_id)

             this.jugadores = this.dataprovider.traejugadores(this.club_id,"null")

            }

           })


  		
    console.log('Hello HistorialComponent Component');
    this.text = 'Hello World';
  }


  presentModal(data) {
    const modal = this.modalCtrl.create(FiltrohistorialComponent, {'jugador':data});
    modal.present();
  }

}



